<template>
    <div class="tab-rich-text" id="tab-rich-text" >
        <a :href="item.link == 'undefined' || item.link == '' ? 'javascript:;': item.link"  v-for="item in JSON.parse(data)" >
            <img :src="ApiConfig.domain + item.img " width="100%" style="display:block" alt="">
        </a>
    </div>
</template>
<script>
export default {
    name: 'RichTextComponent',
    props: ['data'],
}
</script>



