<template>
    <div class="tab-chat">
        <!--  Start -->
        <ul class="chat-msg" id="chat-msg"></ul>
        <!--  End -->

        <!--  Start -->
        <div class="chat-box" id="chat-box">

        </div>
        <!--  End -->

        <!--  Start -->
        <div class="chat-bar" id="chat-bar">
            <a href="" class="chat-back bg-tab_back"></a>
            <input type="text" class="chat-input" id="chat-input" placeholder="~">
            <div class="chat-send" id="chat-send">
                <a href="" class="chat-more bg-tab_more active"></a>
                <div class="chat-submit"></div>
            </div>
            <a href="javascript:;" class="bg-tab_gifts" v-on:click="gift.active = true"></a>
        </div>
        <!--  Start -->

        
        <gift-component v-show="gift.active" :data="gift.data" v-if="gift.data.length" v-on:close-gift-window="gift.active = false"></gift-component>

        <!-- <img src="img/gif/1.gif" alt="" class="gifts-gif"> -->
    </div>
</template>
<script>
import GiftComponent from '@/components/GiftComponent.vue'

import {mapMutations, mapState, mapGetters} from 'vuex'

export default {
    data(){
        return {
            gift:{
                active: false,
                data: {}
            },
        }
    },
    computed:{
        ...mapState({
            chnid: state => state.BaseInfoService.chnid,
            baseInfo: state => state.BaseInfoService.baseInfo,
        })
    },
    methods:{
        closeGiftWindow(){
            console.log(123)
            this.gift.active = false;
        }
    },
    components:{
        GiftComponent
    },
    mounted: function(){
        this.$nextTick(function () {
            this.$store.dispatch('getGiftListJson',{
                chnid: this.chnid
            }).then(({data})=>{
                this.gift.data = data;
            })
        })
    }
}
</script>




