<template>
    <div class="show-app app-container">
		<video-component :video="video"></video-component>
        <notice-component :word="notice.word"></notice-component>
        <component :is="currentComponent"></component>
	</div>
</template>
<script>

import VideoComponent from '@/components/VideoComponent.vue'
import NoticeComponent from '@/components/NoticeComponent.vue'
import WaitComponent from '@/components/WaitComponent.vue'
import TabComponent from '@/components/TabComponent.vue'

import {mapMutations, mapState, mapGetters} from 'vuex'

export default {
    name: 'ShowPage',
    data(){
        return {
            video:{
                src: './static/media/test.mp4',
                poster: '',
                type: 'application/x-mpegURL',
            },
            notice:{
                word: ''
            },
            currentComponent: null
        }
    },
    computed:{
        ...mapState({
            chnid: state => state.BaseInfoService.chnid,
            baseInfo: state => state.BaseInfoService.baseInfo,
        })
    },
    watch:{
        baseInfo: function(data){
            if(data.airtime && data.airtime > parseInt(new Date().getTime()/1000)){
                this.currentComponent = 'wait-component'
            }else{
                this.currentComponent = 'tab-component'
            }
        }
    },
    components:{
        VideoComponent,
        NoticeComponent,
        WaitComponent,
        TabComponent    
    },
    mounted: function(){
        this.$nextTick(()=>{
            this.$store.commit('setChnid',this.$route.params.chnid);
            this.$store.dispatch('getVideoInitialJson',{
                chnid: this.chnid,
            }).then(({data}) => {
                this.$store.commit('setBaseInfo',data);
                this.video.poster = this.ApiConfig.domain + data.topimg;
                document.title = data.title;
            })

            this.$store.dispatch('getLiveUrlInfo',{
                chnid: this.chnid,
            }).then(({data}) => {
                if(data.success == "true"){
                    this.video.src = data.uri


                }else{
                    // alert(data.msg);
                }
            })
        })
    }
}
</script>



