import Vue from 'vue'
import Router from 'vue-router'
import LoginPage from '@/pages/LoginPage'
import IndexPage from '@/pages/IndexPage'
import ShowPage from '@/pages/ShowPage'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/login',
      name: 'LoginPage',
      component: LoginPage
    },
    {
      path: '/index',
      name: 'IndexPage',
      component: IndexPage
    },
    {
      path: '/show/chnid/:chnid',
      name: 'ShowPage',
      component: ShowPage
    }
  ]
})



